import React, { Component } from 'react';
import { Card, Row, Col, message } from 'antd';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getEnrollSolutions, deleteEnrollSolutions } from 'services/listEnrollmentCondition';

class ListEnrollmentPlanCondition extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
    };
  }

  handleDelete = async id => {
    await deleteEnrollSolutions(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Mã phương án',
        dataIndex: 'id',
        align: 'center',
        key: 'id',
      },
      {
        title: 'Tên phương án',
        dataIndex: 'label',
        align: 'center',
        key: 'label',
      },
      {
        title: 'Khu vực tuyển sinh',
        dataIndex: 'courseId',
        align: 'center',
        key: 'courseId',
      },
      {
        title: 'Xét theo',
        dataIndex: 'reviewTypeId',
        align: 'center',
        render: (text, record) => {
          if (record.reviewTypeId === 0) {
            return <div>Học bạ</div>;
          }
          return <div>Kỳ thi THPTQG</div>;
        },
      },
      {
        title: 'Điểm tổng',
        dataIndex: 'minTotalPoint',
        align: 'center',
        key: 'minTotalPoint',
      },
      {
        title: 'Điểm chi tiết',
        dataIndex: 'minPointPerSubject',
        align: 'center',
        key: 'minPointPerSubject',
      },
      {
        title: 'Số lượng tuyển sinh',
        dataIndex: 'noOfStudent',
        align: 'center',
        key: 'noOfStudent',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/enrollment-plan-condition/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/enrollment-plan-condition/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getEnrollSolutions} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default ListEnrollmentPlanCondition;



